.sp-container
  position: absolute
  top: 0
  left: 0
  display: inline-block
  *display: inline
  *zoom: 1
  /* https://github.com/bgrins/spectrum/issues/40
  z-index: 9999994
  overflow: hidden
  &.sp-flat
    position: relative
  box-sizing: content-box
  *
    box-sizing: content-box

/* Fix for * { box-sizing: border-box; }

/* http://ansciath.tumblr.com/post/7347495869/css-aspect-ratio

.sp-top
  position: relative
  width: 100%
  display: inline-block

.sp-top-inner
  position: absolute
  top: 0
  left: 0
  bottom: 0
  right: 0

.sp-color
  position: absolute
  top: 0
  left: 0
  bottom: 0
  right: 20%

.sp-hue
  position: absolute
  top: 0
  right: 0
  bottom: 0
  left: 84%
  height: 100%

.sp-clear-enabled .sp-hue
  top: 33px
  height: 77.5%

.sp-fill
  padding-top: 80%

.sp-sat, .sp-val
  position: absolute
  top: 0
  left: 0
  right: 0
  bottom: 0

.sp-alpha-enabled
  .sp-top
    margin-bottom: 18px
  .sp-alpha
    display: block

.sp-alpha-handle
  position: absolute
  top: -4px
  bottom: -4px
  width: 6px
  left: 50%
  cursor: pointer
  border: 1px solid black
  background: white
  opacity: .8

.sp-alpha
  display: none
  position: absolute
  bottom: -14px
  right: 0
  left: 0
  height: 8px

.sp-alpha-inner
  border: solid 1px #333

.sp-clear
  display: none
  &.sp-clear-display
    background-position: center

.sp-clear-enabled .sp-clear
  display: block
  position: absolute
  top: 0px
  right: 0
  bottom: 0
  left: 84%
  height: 28px

/* Don't allow text selection

.sp-container, .sp-replacer, .sp-preview, .sp-dragger, .sp-slider, .sp-alpha, .sp-clear, .sp-alpha-handle
  -webkit-user-select: none
  -moz-user-select: -moz-none
  -o-user-select: none
  user-select: none

.sp-container
  &.sp-dragging .sp-input, button
    -webkit-user-select: none
    -moz-user-select: -moz-none
    -o-user-select: none
    user-select: none
  &.sp-input-disabled .sp-input-container, &.sp-buttons-disabled .sp-button-container, &.sp-palette-buttons-disabled .sp-palette-button-container
    display: none

.sp-palette-only .sp-picker-container, .sp-palette-disabled .sp-palette-container, .sp-initial-disabled .sp-initial
  display: none

/* Gradients for hue, saturation and value instead of images.  Not pretty... but it works

.sp-sat
  background-image: linear-gradient(to right, white, rgba(204, 154, 129, 0))
  -ms-filter: "progid:DXImageTransform.Microsoft.gradient(GradientType = 1, startColorstr=#FFFFFFFF, endColorstr=#00CC9A81)"
  filter: progid:DXImageTransform.Microsoft.gradient(GradientType = 1, startColorstr='#FFFFFFFF', endColorstr='#00CC9A81')

.sp-val
  background-image: linear-gradient(to top, black, rgba(204, 154, 129, 0))
  -ms-filter: "progid:DXImageTransform.Microsoft.gradient(startColorstr=#00CC9A81, endColorstr=#FF000000)"
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#00CC9A81', endColorstr='#FF000000')

.sp-hue
  background: linear-gradient(to bottom, red 0%, yellow 17%, lime 33%, cyan 50%, blue 67%, magenta 83%, red 100%)

/* IE filters do not support multiple color stops.
 * Generate 6 divs, line them up, and do two color gradients for each.
 * Yes, really.

.sp-1
  height: 17%
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#ff0000', endColorstr='#ffff00')

.sp-2
  height: 16%
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#ffff00', endColorstr='#00ff00')

.sp-3
  height: 17%
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#00ff00', endColorstr='#00ffff')

.sp-4
  height: 17%
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#00ffff', endColorstr='#0000ff')

.sp-5
  height: 16%
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#0000ff', endColorstr='#ff00ff')

.sp-6
  height: 17%
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#ff00ff', endColorstr='#ff0000')

.sp-hidden
  display: none !important

/* Clearfix hack

.sp-cf
  &:before
    content: ""
    display: table
  &:after
    content: ""
    display: table
    clear: both
  *zoom: 1

/* Mobile devices, make hue slider bigger so it is easier to slide
@media (max-device-width: 480px)
  .sp-color
    right: 40%
  .sp-hue
    left: 63%
  .sp-fill
    padding-top: 60%

.sp-dragger
  border-radius: 5px
  height: 5px
  width: 5px
  border: 1px solid #fff
  background: #000
  cursor: pointer
  position: absolute
  top: 0
  left: 0

.sp-slider
  position: absolute
  top: 0
  cursor: pointer
  height: 3px
  left: -1px
  right: -1px
  border: 1px solid #000
  background: white
  opacity: .8

/*
 *Theme authors:
 *Here are the basic themeable display options (colors, fonts, global widths).
 *See http://bgrins.github.io/spectrum/themes/ for instructions.

.sp-container
  border-radius: 0
  background-color: #ECECEC
  border: solid 1px #f0c49B
  padding: 0
  font: normal 12px "Lucida Grande", "Lucida Sans Unicode", "Lucida Sans", Geneva, Verdana, sans-serif
  box-sizing: border-box
  button, input
    font: normal 12px "Lucida Grande", "Lucida Sans Unicode", "Lucida Sans", Geneva, Verdana, sans-serif
    box-sizing: border-box

.sp-color, .sp-hue, .sp-clear
  font: normal 12px "Lucida Grande", "Lucida Sans Unicode", "Lucida Sans", Geneva, Verdana, sans-serif
  box-sizing: border-box

.sp-top
  margin-bottom: 3px

.sp-color, .sp-hue, .sp-clear
  border: solid 1px #666

/* Input

.sp-input-container
  float: right
  width: 100px
  margin-bottom: 4px

.sp-initial-disabled .sp-input-container
  width: 100%

.sp-input
  font-size: 12px !important
  border: 1px inset
  padding: 4px 5px
  margin: 0
  width: 100%
  background: transparent
  border-radius: 3px
  color: #222
  &:focus
    border: 1px solid orange
  &.sp-validation-error
    border: 1px solid red
    background: #fdd

.sp-picker-container, .sp-palette-container
  float: left
  position: relative
  padding: 10px
  padding-bottom: 300px
  margin-bottom: -290px

.sp-picker-container
  width: 172px
  border-left: solid 1px #fff

/* Palettes

.sp-palette-container
  border-right: solid 1px #ccc

.sp-palette-only .sp-palette-container
  border: 0

.sp-palette .sp-thumb-el
  display: block
  position: relative
  float: left
  width: 24px
  height: 15px
  margin: 3px
  cursor: pointer
  border: solid 2px transparent
  &:hover, &.sp-thumb-active
    border-color: orange

.sp-thumb-el
  position: relative

/* Initial

.sp-initial
  float: left
  border: solid 1px #333
  span
    width: 30px
    height: 25px
    border: none
    display: block
    float: left
    margin: 0
  .sp-clear-display
    background-position: center

/* Buttons

.sp-palette-button-container, .sp-button-container
  float: right

/* Replacer (the little preview div that shows up instead of the <input>)

.sp-replacer
  margin: 0
  overflow: hidden
  cursor: pointer
  padding: 0
  display: inline-block
  *zoom: 1
  *display: inline
  //border: solid 1px #91765d
  //background: #eee
  color: #333
  vertical-align: middle
  &:hover, &.sp-active
    border-color: #F0C49B
    color: #111
  &.sp-disabled
    cursor: default
    border-color: silver
    color: silver

.sp-dd
  padding: 2px 0
  height: 16px
  line-height: 16px
  float: left
  font-size: 6px
  color: #fff

.sp-preview
  position: relative
  width: 15px
  height: 15px
  border: solid 1px #222
  margin-right: 5px
  float: left
  z-index: 0

.sp-palette
  *width: 220px
  max-width: 220px
  .sp-thumb-el
    width: 16px
    height: 16px
    margin: 2px 1px
    border: solid 1px #d0d0d0

.sp-container
  padding-bottom: 0
  button
    background-color: #eeeeee
    background-image: linear-gradient(to bottom, #eeeeee, #cccccc)
    border: 1px solid #ccc
    border-bottom: 1px solid #bbb
    border-radius: 3px
    color: #333
    font-size: 14px
    line-height: 1
    padding: 5px 4px
    text-align: center
    text-shadow: 0 1px 0 #eee
    vertical-align: middle
    &:hover
      background-color: #dddddd
      background-image: linear-gradient(to bottom, #dddddd, #bbbbbb)
      border: 1px solid #bbb
      border-bottom: 1px solid #999
      cursor: pointer
      text-shadow: 0 1px 0 #ddd
    &:active
      border: 1px solid #aaa
      border-bottom: 1px solid #888
      box-shadow: inset 0 0 5px 2px #aaaaaa, 0 1px 0 0 #eeeeee

/* Buttons: http://hellohappy.org/css3-buttons/

.sp-cancel
  font-size: 11px
  color: #d93f3f !important
  margin: 0
  padding: 2px
  margin-right: 5px
  vertical-align: middle
  text-decoration: none
  &:hover
    color: #d93f3f !important
    text-decoration: underline

.sp-palette span
  &:hover, &.sp-thumb-active
    border-color: #000

.sp-preview, .sp-alpha, .sp-thumb-el
  position: relative
  background-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAwAAAAMCAIAAADZF8uwAAAAGUlEQVQYV2M4gwH+YwCGIasIUwhT25BVBADtzYNYrHvv4gAAAABJRU5ErkJggg==)

.sp-preview-inner, .sp-alpha-inner, .sp-thumb-inner
  display: block
  position: absolute
  top: 0
  left: 0
  bottom: 0
  right: 0

.sp-palette
  .sp-thumb-inner
    background-position: 50% 50%
    background-repeat: no-repeat
  .sp-thumb-light.sp-thumb-active .sp-thumb-inner
    background-image: url(data:image/png;base64,)
  .sp-thumb-dark.sp-thumb-active .sp-thumb-inner
    background-image: url(data:image/png;base64,)

.sp-clear-display
  background-repeat: no-repeat
  background-position: center
  background-image: url(data:image/gif;base64,)